import { Paper, Stack, Typography } from "@mui/material";
import styles from "./Welcome.module.scss";
import variables from "../scss/variables.module.scss";

const fullOpaqueCSS = {
  bgcolor: variables.transparent,
};

const partialOpaqueCSS = {
  bgcolor: variables.transparentPartial,
};
const Welcome = () => {
  return (
    <Paper sx={fullOpaqueCSS} className={styles.wrapper}>
      <Paper sx={partialOpaqueCSS} className={`${styles.glass} ${styles.format}`}>
        <Stack spacing={6} pb={3}>
          <Typography variant="h1">Tere tulemast!</Typography>
          <Typography>See veebileht on osati valminud Tartu likooli operatsioonissteemide aine materjalide raames.</Typography>
          <Typography variant="body1">
            Iga hetk, mil arvuti ttab, kib selle sees le 200 erineva protsessi. Et igapevaseid tegevusi oleks vimalikult
            mugav arvutis teha (ilma, et arvuti hanguks), peab operatsioonissteem kiki sissetulevaid ja kimasolevaid programme
            haldama. Protsessihalduse vaatepunktist on kige thtsam, et programmide ooteaeg oleks selles ootejrjekorras
            vimalikult vike. Selle probleemi lahendamiseks on olemas vastavad vastavad algoritmid, lihtsuse mttes vaatame siin
            ainult helimelisi algorimte. Mned neist on First Come First Serve, Shortest Job First ja Round Robin.
          </Typography>
          <Typography variant="body2">
            levalt vasakust menribast vali{" "}
            <strong>
              <i>Protsessihaldus</i>
            </strong>{" "}
            ja testi vastavaid algoritme etteantud vi omaenda mustriga.
          </Typography>
        </Stack>
      </Paper>
    </Paper>
  );
};

export { Welcome };
